import React, { Component } from 'react'
import axios from 'axios' 
import { Link, Redirect } from 'react-router-dom'
import {API_URL} from '../config'

class TodoDetail extends Component {

    // this.props.onDelete = fnc

    state ={
        todo: {}
    }

    componentDidMount() {
       let todoId = this.props.match.params.todoId
       console.log(this.props)
    //    this.props.history.push('/manish.com')
        axios.get(`${API_URL}/todos/${todoId}`)
            .then((response) => {
                this.setState({
                    todo: response.data
                })
            })
    }


    render() {
        const {name, description, image, _id} = this.state.todo
        const {loggedInUser} = this.props

        if (!loggedInUser){
            //render method always return something
            return <Redirect to={'/sign-in'}/>
        }

        return (
            <div>
                <div>Name: {name}</div>
                <div>Detail: {description}</div>
                <img style= {{height: "100px"}} src={image}/>
                <br/> 
                <Link to={`/todo/${_id}/edit`} ><button>Edit</button></Link>
                <button onClick={() => { this.props.onDelete(_id) } }>Delete</button>
            </div>
        )
    }
}



export default TodoDetail